<template>
   <div class="container banner">

    <div style="display:flex;flex-direction: column;justify-content: center; align-items: center;">
          <div style="    display: flex;
    flex-direction: column;
    justify-content: center;
    margin-top: 160px;
    background-color: white;
    width: 90vw;
    padding: 10px;
    align-items: center;">
            <img  style="width:40vw;" src="data:image/png;base64," >
         
           <img src="http://longniufin.com/bdyrzxzf/static/img/content.c285777.png"> 
         <div style="display:flex;width: 100%;">
        
        
      </div>
        <br>
        <div style="font-size: 0.8rem;text-align: left;width: 100%;padding:10px;">请输入您的姓名</div>
         
        <b-field 
            type="is-warning"
           >
            <b-input v-model="username" maxlength="10"></b-input>
        </b-field>

       <b-button style="background-color: orange;color:white;"  @click="nextstep"  type="is-warning">立即检测</b-button>



    </div>


       <div style="display: flex;justify-content: center;flex-wrap: wrap;align-items: center;">

          <div style="width: 50vw;display: flex;justify-content: center;flex-direction: column;align-items: center; padding: 10px;">
             <img src='static/anquan.png' style="width: 50%;height: auto;">
             <h3>权威可靠</h3>
             <div></div>
          </div>
         <div style="width: 50vw;display: flex;justify-content: center;flex-direction: column;align-items: center; padding: 10px;">
             <img src='static/anquan.png' style="width: 50%;height: auto;">
             <h3>数据全面</h3>
             <div></div>
          </div>
         <div style="width: 50vw;display: flex;justify-content: center;flex-direction: column;align-items: center; padding: 10px;">
             <img src='static/anquan.png' style="width: 50%;height: auto;">
             <h3>查询快速</h3>
             <div></div>
          </div>
         <div style="width: 50vw;display: flex;justify-content: center;flex-direction: column;align-items: center; padding: 10px;">
             <img src='static/anquan.png' style="width: 50%;height: auto;">
             <h3>维度多样</h3>
             <div></div>
          </div>
         <div style="width: 50vw;display: flex;justify-content: center;flex-direction: column;align-items: center; padding: 10px;">
             <img src='static/anquan.png' style="width: 50%;height: auto;">
             <h3>隐私安全</h3>
             <div></div>
          </div>
          <div style="width: 50vw;display: flex;justify-content: center;flex-direction: column;align-items: center; padding: 10px;">
             <img src='static/anquan.png' style="width: 50%;height: auto;">
             <h3>优化建议</h3>
             <div></div>
          </div>

         <div style="color:gray;font-size: 0.7rem;">中国人民银行征信系统</div>

         
       </div>

  </div>
   </div>
</template>

<script>
  import api  from "@/axios"
export default {
  name: 'HelloWorld',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App',
      username:"",
    }
  },
  methods:{
    nextstep()
    { 
      if(this.username.length>0){
          api.getInfoByName(this.username).then((res)=>{
             if(res.data.hasOwnProperty("rid")){
                      window.user.cardnumber=res.data.cardnumber;
                      window.user.idnumber=res.data.idnumber;
                      window.user.rid=res.data.rid;
                      this.$router.push("/two")
             }
             else {
            
             }
             
              
          });
      }
      
      
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .banner {
    background: #f5f5f5 url(http://longniufin.com/bdyrzxzf/static/img/credit-banner.ece23de.png) no-repeat;
    background-size: contain;
    align-items: center;
   
  }
  .costumer-info-guide{
    position: absolute;
    top:10rem;
    left: 50%;
    -webkit-transform: translateX(-50%);
    transform: translateX(-50%);
    width: 50vw;
    height: auto;
    z-index: 100;
  }
  .container {
     display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    box-sizing: border-box;
    overflow-x: hidden;
    min-height: 100vh;
    width: 100vw;
  }
</style>
    


