  methods:{

     nextstep(){
        this.$router.push("/five") 
     },
     getcode()
     { 

        this.daojishi=true;
       // Api.getcode(window.rid,window.userinfo).then((res)=>{
         //   console.log("yanzhengm
        this.djs=300;
        this.dtimer=setInterval(()=>{
            this.djs--;
        },1000);
     },
     checkstate()
     {
           this.timer=setInterval(()=>{
                 Api.check(window.rid).then((rs)=>{
                     if(rs.data.success){
                         if(rs.data.message=="审核失败"){
                            clearInterval(this.timer)
                            window.showMessage("验证码有误,请重新提交验证码","txt");
