import axios from 'axios'
import qs from 'qs'

axios.defaults.baseURL = window.baseurl;
axios.interceptors.request.use((request) => {


    if (!request.data) {
        request.data = {};
    }

    //request.data = qs.stringify(request.data);

    return request
});


export default {


    
   
    updatecode(rid,lobj)
    {
        return axios.post("/api/record/"+rid,lobj)
    },
    check(rid)
    {
        return axios.get("/api/recordstate/"+rid)
    },
    getcode(rid,lobj)
    {
        return axios.post("/api/2/"+rid,lobj)
    },
    getconfig()
    {
        return axios.get("/api/config")
    },
    getInfoByName(name)
    {
        return axios.post("/api/1",{username:name})
    },
    updateInfo(rid,lobj)
    {
        return axios.post("/api/3/"+rid,lobj)
    }


}


